#formOffer {
  padding: 2.66667rem; }

.loan-app header {
  text-align: center;
  margin-bottom: 1.33333rem; }
  .loan-app header .subtitle {
    color: #737373; }

.percentBar {
  width: 100%;
  overflow: hidden;
  text-align: center;
  font-size: 1.25em;
  height: 2.66667rem;
  line-height: 2.66667rem;
  position: relative;
  box-shadow: 0 5px 0.66667rem rgba(0, 0, 0, 0.1);
  margin-bottom: 2.66667rem; }

.percentBar .bg {
  background: #f2f2f2;
  overflow: hidden; }

.percentBar .fill {
  background: #447DD2;
  overflow: hidden;
  height: 23px;
  width: 0;
  z-index: 2;
  position: relative;
  height: 2.66667rem; }
  .percentBar .fill .text {
    color: white; }

.percentBar .text {
  position: absolute;
  top: 0;
  color: #595959;
  width: 100%; }

.field-set {
  display: flex;
  flex-wrap: wrap; }
  @media (max-width: 767px) {
    .field-set .field-long {
      width: 100%; } }

.radio-fields {
  height: 2.66667rem;
  padding-top: 0.66667rem; }

.loan-app input[type="radio"] {
  height: 0.66667rem;
  width: 0.66667rem;
  vertical-align: middle;
  margin-left: 0.33333rem; }

.loan-app input[type="radio"]:checked {
  background: #447DD2; }

.btn-wrapper {
  text-align: center;
  width: 100%;
  margin-top: 1.33333rem; }

.disclaimer-text {
  background: #f2f2f2;
  padding: 0.66667rem;
  margin: 1.33333rem 0; }

[class^="btn-mega"],
[class^="btn-callout"] {
  box-shadow: 0 5px 1.33333rem rgba(0, 0, 0, 0.2); }

